@import "@theme/vars.scss";

.section-documents {

    .section__inner{
        padding: 0 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 0;
        }
    }

    &__list {
        max-width: 970px;

        @media only screen and (max-width: $bp-xl) {
            max-width: none;
        }
    }

    .document{
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 12px;
        }
        
        &:last-child{
            margin-bottom: 0;
        }
    }
}


