@import "@theme/vars.scss";

.promo-slider{
    overflow: visible;

    &__item {
        display: flex;
        width: 100%;
        height: 580px;
        border-radius: 15px;
        overflow: hidden;

        @media only screen and (max-width: $bp-xl) {
            height: 480px;
        }

        @media only screen and (max-width: $bp-lg) {
            height: 450px;
        }

        @media only screen and (max-width: $bp-md) {
            height: auto !important;
            width: 100%;            
        }
    }

    &__item-img {
        display: flex;
        width: 100%;
        height: 100%;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            
            @media only screen and (max-width: $bp-md) {
                object-fit: contain;
            }
        }
    }

    .slider {

        &__inner {
            overflow: visible;
        }
    
        &__slide {
            opacity: 0;
            transition: opacity 0.65s ease;
            transition-delay: 0.15s;

            &>a{
                pointer-events: none;
            }

            &.swiper-slide-active{
                opacity: 1;
                transition-delay: 0s;
                transition-duration: 0.8s;
                    
                &>a{
                    pointer-events: all;
                }
            }
        }

        &__navigation {
            
            @media only screen and (max-width: $bp-xl) {
                display: none;
            }
        }
    }
}