@import "@theme/vars.scss";

.document {
    display: flex;

    &__icon {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        width: 80px;
        height: 80px;
        background: linear-gradient(336.84deg, rgba(0, 48, 66, 0.128) -13.89%, rgba(0, 48, 66, 0.012) 111.56%), #FFFFFF;
        border-radius: 8px;
        margin-right: 10px;

        @media only screen and (max-width: $bp-lg) {
            width: 72px;
            height: 72px;
        }

        .icon{
            width: 42px;
            height: 42px;
            flex-shrink: 0;
        }
    }

    &__main {
        margin: auto 0;
    }

    &__title {
        color: $blue-dark;
        margin-bottom: 5px;
    }

    &__actions {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    &__link {
        display: inline-flex;
        align-items: center;
        text-decoration: none;
        fill: #0087B2;
        color: #0087B2;
        font-family: 'Helvetica';
        transition: fill 0.35s ease, color 0.35s ease;
        margin-right: 10px;

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
            line-height: 19px;
        }

        &:last-of-type{
            margin-right: 0;
        }

        .icon{
            width: 24px;
            height: 24px;
            margin-right: 2px;
        }

        span{

        }

        &:hover{
            color: $blue-dark;
            fill: $blue-dark;
        }
    }
}