@import "@theme/vars.scss";

.television {
    padding-top: 20px;
    &__intro {
        box-sizing: border-box;
        position: relative;
        display: flex;
        align-items: center;
        padding: 40px;
        min-height: 302px;
        background: linear-gradient(14.5deg, rgba(0, 48, 66, 0.06) -21.24%, rgba(0, 48, 66, 0) 176.31%), #FFFFFF;
        border-radius: 15px;
        border: 1px solid #AFBECB;
        color: $blue-dark;


        @media only screen and (max-width: $bp-lg) {
            min-height: 272px;
            padding: 30px;
        }
    }

    &__intro-main {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        max-width: 50%;

        @media only screen and (max-width: $bp-lg) {
            max-width: 70%;
        }

        @media only screen and (max-width: $bp-md) {
            max-width: none;
        }
    }

    &__intro-title {
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 10px;
        }
    }

    &__intro-subtitle {
        font-size: 22px;
        line-height: 135%;
        font-family: 'Helvetica';
        margin-bottom: 30px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 18px;
            margin-bottom: 20px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 16px;
        }

        span{
            font-weight: 700;
        }
    }

    &__intro-price {
        margin-bottom: 15px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
            line-height: 145%;
            margin-bottom: 10px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 145%;
        }
    }

    &__intro-button {

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
        }
    }

    &__intro-img {
        display: flex;
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 50%;

        @media only screen and (max-width: $bp-lg) {
            height: 60%;
            width: 30%;
            top: auto;
            bottom: 0;
        }

        @media only screen and (max-width: $bp-md) {
            width: 50%;
            height: 50%;
        }

        img{
            width: 100%;
            max-height: 100%;
            object-fit: contain;
            object-position: right center;

            @media only screen and (max-width: $bp-lg) {
                object-position: right bottom;
            }
        }
    }

    &__content {
        box-sizing: border-box;
        margin-top: 30px;
        padding: 0 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 0;
        }
    }

    &__description {
        max-width: 900px;
        color: $blue-dark;

        &>*{
            margin-bottom: 20px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 15px;
            }

            @media only screen and (max-width: $bp-md) {
                margin-bottom: 10px;
            }

            &:last-child{
                margin-bottom: 0;
            }
        }

        p{
            font-family: 'CenturyGothic';
            font-size: 16px;
            line-height: 155%;

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
                line-height: 140%;
            }
        }


    }

    &__options {
        display: flex;
        flex-direction: column;
    }

    &__option {
        display: flex;
        margin-bottom: 10px;

        &:last-of-type{
            margin-bottom: 0;
        }

        p{
            margin-bottom: 0;

            &:first-of-type{
                font-weight: 700;
            }

            &:last-of-type{
                margin-left: 10px;
            }
        }
    }

    &__documents {
        margin-top: 30px;

        .document{
            margin-bottom: 20px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 10px;
            }

            &:last-of-type{
                margin-bottom: 0;
            }
        }
    }
}
