@import "@theme/vars.scss";
@import "@theme/animations.scss";

.header {
    box-sizing: border-box;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    padding: 10px 0;
    transition: background-color 0.5s ease;
    background-color: #fff;
    padding-right: inherit;

    &.is-fixed{
        // background-color: rgba(0, 0, 0, 0.35);
    }

    &.is-active{
        // background-color: #fff;
    }

    &__container {
    }

    &__inner {
        display: flex;
        align-items: center;
    }

    &__burger-trigger {
        cursor: pointer;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        flex-shrink: 0;
        width: 33px;
        margin-right: 28px;
        height: 19px;

        @media only screen and (max-width: $bp-md) {
            margin-right: 15px;
        }

        &:hover {
            span,
            &::after,
            &::before {
                background-color: $blue-light;
            }
        }


        span,
        &::after,
        &::before {
            content: "";
            display: block;
            height: 3px;
            width: 33px;
            transform-origin: center;
            background-color: $blue;
            transition: transform 0.35s ease, opacity 0.35s ease, background-color 0.35s ease;
        }
    }

    &__logo {
        display: flex;
        align-items: center;
        width: 109px;
        height: 42px;
        
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__actions {
        display: flex;
        align-items: center;
        margin-left: auto;

        &>*{
            margin-left: 20px;

            @media only screen and (max-width: $bp-md) {
                margin-left: 15px;
            }

            &:first-child{
                margin-left: 0;
            }
        }
    }

    &__phone {
        display: flex;
        align-items: center;
        text-decoration: none;
        font-family: 'HelveticaNeue';
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        color: $dark;
        transition: text-shadow 0.4s ease;

        &:hover{
            text-shadow: 0px 0px 0.5px $dark;
        }

        span{
            margin-left: 5px;

            @media only screen and (max-width: $bp-md) {
                display: none;
            }
        }

        .icon{
            width: 25px;
            height: 25px;

            @media only screen and (max-width: $bp-md) {
                width: 30px;
                height: 30px;
            }
        }
    }

    &__login {
        box-sizing: border-box;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 42px;
        padding: 0 14px;
        border-radius: 5px;
        background-color: $gold;
        text-decoration: none;
        font-family: 'HelveticaNeue';
        font-size: 16px;
        line-height: 19px;
        color: $dark;
        transition: text-shadow 0.35s ease;

        &:hover{
            text-shadow: 0px 0px 0.5px $dark;
        }

        @media only screen and (max-width: $bp-md) {
            background-color: transparent;
            padding: 0;
            border-radius: 0;
            height: auto;
        }

        span{
            @media only screen and (max-width: $bp-md) {
                display: none;
            }
        }

        .icon{
            display: none;

            @media only screen and (max-width: $bp-md) {
                display: block;
                width: 30px;
                height: 30px;
            }
        }
    }

    &__burger {
        display: flex;
        flex-direction: column;
        background-color: #fff;
        position: absolute; 
        top: 0;
        left: 0;
        width: 395px;
        height: 100vh;
        z-index: 2;
        transform: translateX(-100%);
        transition: transform 0.65s ease, box-shadow 0.25s ease;
        transition-delay: 0s, 0.4s;

        &.is-active{
            transform: translateX(0%);
            box-shadow: 26px 4px 100px rgba(0, 0, 0, 0.25);
            transition-delay: 0s;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            height: 100vh;
        }
    }

    &__burger.is-active &__burger-logo{
        transform: translateX(0%);
        opacity: 1;
        transition-delay: 0.2s;
    }

    &__burger.is-active &__burger-close{
        transform: translateX(0%);
        opacity: 1;
        transition-delay:0s, 0.2s, 0.2s;
    }

    &__burger.is-active &__burger-body{
        transform: translateX(0%);
        transition-delay: 0.35s;
    }

    &__burger-inner {
    }

    &__burger-header {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid rgba(162, 182, 196, 0.5);
        padding: 12px 40px;
    }

    &__burger-logo {
        width: 122px;
        height: 59px;
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        transform: translateX(-150px);
        transition: transform 0.65s ease, opacity 0.65s ease;

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        @media only screen and (max-width: $bp-md) {
            width: 104px;
            height: 50px;
        }
    }

    &__burger-close {
        cursor: pointer;
        flex-shrink: 0;
        width: 26px;
        height: 26px;
        fill: $blue;
        transform: translateX(-150px);
        opacity: 0;
        transition: fill 0.35s ease, transform 0.65s ease, opacity 0.65s ease;

        &:hover{
            fill: $blue-light;
        }
    }

    &__burger-body {
        display: flex;
        flex-direction: column;
        overflow-y: auto;
        padding: 30px 40px;
        transition: transform 0.65s ease, opacity 0.65s ease;
        transform: translateX(-150px);
    }

    &__nav {

        .nav {
            display: flex;
            flex-direction: column;

            &__chapter {
                display: flex;
                flex-direction: column;
                margin-bottom: 20px;

                &:last-of-type{
                    margin-bottom: 0;
                }
            }
        
            &__title {
                font-size: 14px;
                line-height: 17px;
                text-transform: uppercase;
                color: $dark;
                margin-bottom: 15px;
            }
        
            &__item {
                display: flex;
                text-decoration: none;
                color: $gray;
                font-family: 'HelveticaNeue';
                font-size: 16px;
                line-height: 19px;
                padding: 5px 0;
                transition: color 0.35s ease;

                &:hover{
                    color: $blue-light;
                }
            }
        }
    }
}