@import '@theme/vars.scss';

.section-articles {

    &__slider{
        position: relative; 
                
        .slider {

            &__inner {
                overflow: visible;
            }

            &__wrapper {
            }

            &__slide {
                opacity: 0;
                transition: opacity 0.65s ease;
                transition-delay: 0.15s;

                &>a{
                    pointer-events: none;
                }

                &.swiper-slide-active{
                    opacity: 1;
                    transition-delay: 0s;
                    transition-duration: 0.8s;
                            
                    &>a{
                        pointer-events: all;
                    }
                }
            }

            &__pagination {
                width: 100%;
                position: absolute;
                bottom: 40px;
                left: 0;
                z-index: 2;
            }
        }
    }
}