@import "@theme/vars.scss";

.policy {   

    .section{
        &__inner{
            padding: 0 32px;

            @media only screen and (max-width: $bp-lg) {
                padding: 0;
            }
        }
    }

    &__block {
        color: $blue-dark;
        margin-bottom: 30px;
        max-width: 1152px;

        h2{
            font-size: 25px;
            line-height: 31px;
            margin-bottom: 20px;
        
            @media only screen and (max-width: $bp-lg) {
                font-size: 22px;
                line-height: 27px;
                margin-bottom: 15px;
            }

            @media only screen and (max-width: $bp-md) {
                font-size: 18px;
                line-height: 145%;
            }
                
            &:last-child{
                margin-bottom: 0;
            }
        }

        h3{
            font-size: 18px;
            line-height: 155%;
            margin-bottom: 20px;
        
            @media only screen and (max-width: $bp-lg) {
                font-size: 16px;
                line-height: 155%;
                margin-bottom: 10px;
            }

            @media only screen and (max-width: $bp-md) {
                font-size: 14px;
                line-height: 155%;
            }
                
            &:last-child{
                margin-bottom: 0;
            }
        }

        p{
            font-size: 16px;
            line-height: 155%;
            margin-bottom: 10px;

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
                line-height: 145%;
            }

            span{
                font-weight: 700;
            }

            &:last-child{
                margin-bottom: 0;
            }
        }

        a{
            font-weight: 700;
            color: $blue-light;
            transition: color 0.35s ease;

            &:hover{
                color: $blue;
            }
        }

        ul{
            display: flex;
            flex-direction: column;
            padding: 0 0 0 45px;
            margin: 0 0 20px 0;

            @media only screen and (max-width: $bp-xl) {
                padding-left: 20px;
            }

            @media only screen and (max-width: $bp-lg) {
                padding-left: 0;
            }

            &:last-child{
                margin-bottom: 0;
            }
            
            li{
                display: flex;
                align-items: flex-start;
                color: $blue;
                padding: 0;
                margin: 0 0 10px;

                @media only screen and (max-width: $bp-md) {
                    margin-bottom: 5px;
                }
        
                &:last-child{
                    margin-bottom: 0;
                }
        
                &::before{
                    content: "";
                    display: inline-flex;
                    flex-shrink: 0;
                    width: 5px;
                    height: 5px;
                    border-radius: 50%;
                    background-color: $blue;
                    margin: 7.5px 10px;
                }
            }
        }
    }
}
