@import "@theme/vars.scss";

.statistics {

    &__select {
    }
}

.select{
    min-width: 115px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;

    @media only screen and (max-width: $bp-lg){
        font-size: 14px;    
        line-height: 20px;
    }

    @media only screen and (max-width: $bp-md){
        min-width: 98px;
    }

    .custom-select-container.is-open {

        .custom-select-opener{
            border-color: $blue-light;

            &::after{
                content:"";
                transform: translateY(-50%) rotate(-180deg);
            }
        }

        .custom-select-panel{
            visibility: visible;
            opacity: 1;
            transform: translateY(16px);
        }
    }

    .custom-select-container{
        &:hover .custom-select-opener{
            border-color: $blue-light;
        }
    }
    

    .custom-select {
        &-container {
    
        }
        
        &-opener {
            position: relative;
            padding: 6px 38px 6px 14px;
            border: 2px solid #EFEFEF;
            border-radius: 12px;
            background-color: #fff;
            color: #1E1E1E;
            transition: border-color 0.35s ease;

            @media only screen and (max-width: $bp-md){
                padding: 6px 34px 6px 10px;
            }

            &::after{
                content:"";
                display: block;
                width: 16px;
                height: 16px;
                flex-shrink: 0;
                position: absolute;
                top: 50%;
                right: 16px;
                transform: translateY(-50%);
                background-size: 100%;
                background-repeat: no-repeat;
                background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M2.84781 6.73298C3.31156 6.25567 4.06344 6.25567 4.52719 6.73298L12 14.4243L19.4728 6.73298C19.9366 6.25567 20.6884 6.25567 21.1522 6.73298C21.6159 7.21029 21.6159 7.98416 21.1522 8.46146L12.8397 17.017C12.3759 17.4943 11.6241 17.4943 11.1603 17.017L2.84781 8.46146C2.38406 7.98416 2.38406 7.21029 2.84781 6.73298Z' fill='%231E1E1E'/%3E%3C/svg%3E%0A");
                transition: transform 0.35s ease;

                @media only screen and (max-width: $bp-md){
                    right: 12px;
                }
            }
        }

        &-panel {
            width: 300px;
            max-height: 160px !important;
            background-color: #fff;
            box-shadow: 0px 4px 24px rgba(71, 0, 20, 0.08);
            border-radius: 8px;
            right: 0;
            visibility: hidden;
            opacity: 0;
            transform: translateY(0);
            transition: visibility 0.4s ease, opacity 0.4s ease, transform 0.4s ease, max-height 0.4s ease;

            @media only screen and (max-width: $bp-lg){
                max-height: 144px !important;
            }

            @media only screen and (max-width: $bp-md){
                width: 220px;
            }


            &::-webkit-scrollbar {
                height: 4px;
                width: 4px;
            }
        }
        
        &-option {
            font-weight: 500;
            cursor: pointer;
            transition: width 0.35s ease, background-color 0.25s ease;
            padding: 8px 16px;
            color: #001841;

            @media only screen and (max-width: $bp-md){
                padding: 8px 12px;
            }

            &::before{
                content: none !important;
            }

            &.has-focus {
                background-color: rgba(254, 28, 93, 0.15);
            }

            &.is-selected{
                background-color: rgba(254, 28, 93, 0.15);
            }
        }
    }
}
