@import "@theme/vars.scss";

.section-list {

    &__content {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -7px -14px;

        @media only screen and (max-width: $bp-md) {
            margin: 0 0 -10px;
        }
    }

    &__item {
        box-sizing: border-box;
        display: block;
        padding: 0 7px;
        margin-bottom: 14px;
        width: 25%;
        height: 450px;

        @media only screen and (max-width: $bp-xl) {
            width: 50%;
            height: 320px;
        }
        
        @media only screen and (max-width: $bp-lg) {
            height: 270px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 180px;
            width: 100%;
            padding: 0;
            margin-bottom: 10px;
        }
    }

    &__card {
        box-sizing: border-box;
        cursor: pointer;
        position: relative;
        display: flex;
        width: 100%;
        height: 100%;
        padding: 30px;
        background: linear-gradient(336.84deg, rgba(0, 48, 66, 0.2) -13.89%, rgba(0, 48, 66, 0) 111.56%), #FFFFFF;
        border-radius: 15px;
        text-decoration: none;
        color: $blue-dark;
        overflow: hidden;
    }

    &__card-inner {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        max-width: 205px;

        @media only screen and (max-width: $bp-xl) {
            max-width: none;
        }
    }

    &__card-title {
        margin-bottom: 20px;
        
        @media only screen and (max-width: $bp-md) {
            margin-bottom: 10px;
        }
    }

    &__card-text {
    }

    &__card-img {
        pointer-events: none;
        display: flex;
        justify-content: flex-end;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        img{
            max-width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__articles {
        display: block;
        width: 100%;
        height: 100%;
        position: relative;

        .card-article {
            height: 100%;
            padding: 115px 30px 68px 30px;


            @media only screen and (max-width: $bp-xl) {
                padding-top: 85px;
            }

            @media only screen and (max-width: $bp-md) {
                min-height: 180px;
                padding-top: 55px;
                padding-bottom: 55px;
            }

            &__inner {
            }
        
            &__info {
            }
        
            &__date {
                font-size: 18px;
                line-height: 21px;

                @media only screen and (max-width: $bp-xl) {
                    font-size: 16px;
                    line-height: 19px;
                }

                @media only screen and (max-width: $bp-lg) {
                    font-size: 14px;
                    line-height: 17px;
                }

                @media only screen and (max-width: $bp-md) {
                    font-size: 12px;
                    line-height: 15px;
                }
            }
        
            &__title {
                @media only screen and (max-width: $bp-md) {
                    -webkit-line-clamp: 2;
                }
            }
        
            &__link {
                display: none;
            }
        }
    }

    &__articles-title {
        position: absolute;
        top: 30px;
        left: 30px;
        z-index: 2;
        color: #A2B6C4;

        @media only screen and (max-width: $bp-md) {
            top: 22px;
        }
    }

    &__articles-link {
        position: absolute;
        bottom: 24px;
        right: 50px;
        z-index: 2;

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
            line-height: 17px;
            bottom: 22px;

            .icon{
                width: 20px;
                height: 20px;
            }
        }
    }

    &__articles-slider {
        height: 100%;

        .slider {

            &__inner {
                height: 100%;
            }
        
            &__wrapper {
            }
        
            &__slide {
                height: 100%;
            }
        
            &__pagination {
                padding: 0;
                margin: 0;
                left: 30px;
                bottom: 30px !important;
                max-width: 90px;
                justify-content: flex-start;
                flex-wrap: wrap;
                overflow: hidden;
                
                @media only screen and (max-width: $bp-md) {
                    bottom: 27px !important;
                }
            }
        
            &__pagination-item {
                width: 10px;
                height: 10px;
                margin-right: 10px;
                margin-top: 10px;
                background-color: rgba(162, 182, 196, 0.65);

                &.swiper-pagination-bullet-active{
                    background-color: $blue-light;
                }
            }
        }
    }
}