@import "@theme/vars.scss";

.section-equipment {

    .section{
        &__inner{
            padding: 0 32px;

            @media only screen and (max-width: $bp-lg) {
                padding: 0;
            }
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -7.5px -15px;
        min-width: 100%;


        @media only screen and (max-width: $bp-md) {
            margin: 0 -5px -10px;
        }

        .card-equipment{
            width: calc(25% - 15px);
            margin: 0 7.5px 15px;

            @media only screen and (max-width: $bp-xl) {
                width: calc(33.33% - 15px);
            }

            @media only screen and (max-width: $bp-lg) {
                width: calc(50% - 15px);
            }

            @media only screen and (max-width: $bp-md) {
                width: calc(50% - 10px);
                margin: 0 5px 10px;
            }
        }
    }

    &__more {
        display: flex;
        font-size: 18px;
        margin: 20px auto 0;

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
        }
    }
}
