@import "@theme/vars.scss";

.contacts {

    &__content{
        padding: 0 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 0;

            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        &::after{
            content: "";
            clear: both;
            display: block;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr auto;
        gap: 20px 25px;
        font-family: 'HelveticaNeue';

        @media only screen and (max-width: $bp-md) {
            grid-template-columns: 1fr;
            gap: 15px 0;
        }

        &--1 {
            float: left;
        
        }

        &--2 {
            grid-template-columns: 1fr;
            float: right;

            @media only screen and (max-width: $bp-lg) {
                order: 2;
            }
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        max-width: 480px;

        @media only screen and (max-width: $bp-xl) {
            max-width: 400px;
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: 480px;
        }

        @media only screen and (max-width: $bp-md) {
            max-width: none;
        }
    }

    &__item-title {
        font-weight: 500;
        color: #7D8C99;
        margin-bottom: 10px;
    }

    &__item-content {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        a{
            &:hover{
                color: $blue-light;

                .icon{
                    transform: scale(0.85);
                }
            }
        }
    }

    &__item-link {
        display: inline-flex;
        text-decoration: none;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: $blue-dark;
        margin-right: 10px;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
        }

        &:last-of-type{
            margin-right: 0;
        }

        .icon{
            width: 25px;
            height: 25px;
            transition: transform 0.35s ease;
        }

        span{
            margin: auto 0;
        }

        .icon + span{
            margin-left: 5px;
        }
    }

    &__map {
        margin-top: 30px;
        width: calc(100% + 64px);
        height: 595px;
        border-radius: 20px;
        margin: 30px -32px 0;
        overflow: hidden;

        float: left;

        @media only screen and (max-width: $bp-lg) {
            width: calc(100% + 50px);
            height: 435px;
            margin: 25px -25px;
            border-radius: 0;
        }

        @media only screen and (max-width: $bp-md) {
            height: 395px;
            width: calc(100% + 40px);
            margin: 25px -20px;
        }
    }

    &__map-img {
        width: 100%;
        height: 100%;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }
}