@import "@theme/vars.scss";

.articles-list {
    padding: 30px 0;

    &__inner {
        display: flex;
        flex-direction: column;
    }

    &__slider{
        .slider{
            &__inner{
                overflow: visible;
            }

            &__slide{
                @media only screen and (max-width: $bp-md) {
                    height: 100%;
                    width: 97%;

                    .card-article{
                        height: 100%;
                    }
                }
            }
        }
    }

    &__content {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -12.5px -20px;

        @media only screen and (max-width: $bp-xxl) {
            margin: 0 -12px -20px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin: 0 -10px -16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin: 0 0 -10px;
        }

        .card-article{
            width: calc(25% - 25px);
            margin: 0 12.5px 20px;

            @media only screen and (max-width: $bp-xxl) {
                width: calc(33.33% - 24px);
                margin: 0 12px 20px;
            }

            @media only screen and (max-width: $bp-lg) {
                width: calc(50% - 20px);
                margin: 0 10px 16px;
            }

            @media only screen and (max-width: $bp-md) {
                width: 100%;
                margin: 0 0 10px;
            }
        }
    }

    &__more {
        font-size: 18px;
        margin: 20px auto 0;

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
        }
    }
}
