@import "@theme/vars.scss";

.card-equipment {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 30px;
    border: 1px solid $blue-white;
    border-radius: 15px;
    text-decoration: none;
    transition: border-color 0.45s ease;

    @media only screen and (max-width: $bp-lg) {
        padding: 20px;
    }


    @media only screen and (max-width: $bp-md) {
        padding: 14px;
        border-radius: 10px;
    }

    &__body {
        text-decoration: none;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__body:hover &__title{
        color: $blue-light;
    }

    &__img {
        height: 196px;
        max-width: 100%;
        margin-bottom: 12px;

        @media only screen and (max-width: $bp-lg) {
            height: 150px;
            margin-bottom: 10px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 122px;
            margin-bottom: 8px;
        }

        img{
            height: 100%;
            width: auto;
            max-width: 100%;
            object-fit: contain;
        }
    }

    &__title{
        color: $blue-dark;
        text-align: center;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__footer {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        margin: 0 -10px;

        @media only screen and (max-width: $bp-lg) {
            margin-left: -5px;
            margin-right: -5px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: -3px;
            margin-left: -3px;
        }
    }

    &__price {
        font-family: 'HelveticaNeue';
        font-weight: 500;
        font-size: 22px;
        line-height: 135%;
        color: $blue-light;
        margin: 10px 10px 0;

        @media only screen and (max-width: $bp-xl) {
            font-size: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-weight: 400;
            font-size: 18px;
            margin-right: 5px;
            margin-left: 5px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 16px;
            margin: 5px 3px 0;
        }
    }

    &__link {
        color: $blue-dark;
        margin: 10px 10px 0;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 5px;
            margin-left: 5px;
            font-size: 14px;
        }

        @media only screen and (max-width: $bp-md) {
            margin: 5px 3px 0;
        }
        
        .icon{
            @media only screen and (max-width: $bp-lg) {
                width: 20px;
                height: 20px;
            }

            @media only screen and (max-width: $bp-md) {
                width: 16px;
                height: 16px;
            }
        }

        span + .icon{
            margin-left: 5px;
        }

        &:hover{
            color: $blue-light;
        }
    }
}
