@import "@theme/vars.scss";

.footer {
    overflow: hidden;
    box-sizing: border-box;
    background: #F8F8F8;
    padding: 60px 0;
    font-family: 'HelveticaNeue';

    @media only screen and (max-width: $bp-lg) {
        padding: 30px 0;
    }

    &__container {
    }

    &__inner {
        display: flex;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
        }
    }

    &__logo {
        flex-shrink: 0;
        display: block;
        width: 158px;
        height: 61px;
        margin-right: auto;

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-right: 80px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 30px;
            margin-right: 0;
        }
    }

    &__nav {
        display: block;
        margin-right: auto;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 80px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 25px;
            margin-right: 0;
        }
        
        .nav {
            display: flex;

            &__chapter {
                display: flex;
                flex-direction: column;
                margin-right: 65px;

                @media only screen and (max-width: $bp-lg) {
                    margin-right: 30px;
                }

                &:last-of-type{
                    margin-right: 0;
                }
            }
        
            &__title {
                font-size: 14px;
                line-height: 17px;
                text-transform: uppercase;
                color: $dark;
                margin-bottom: 10px;
            }
        
            &__item {
                display: flex;
                text-decoration: none;
                color: $gray;
                white-space: nowrap;
                font-family: 'HelveticaNeue';
                font-size: 16px;
                line-height: 19px;
                padding: 5px 0;
                transition: color 0.35s ease;

                &:hover{
                    color: $blue-light;
                }

                @media only screen and (max-width: $bp-md) {
                    font-size: 14px;
                    line-height: 17px;
                }
            }
        }
    }

    &__contacts {
        margin-right: auto;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 0;
        }
    }

    &__contacts-title {
        color: $dark;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        margin-bottom: 15px;
    }

    &__contacts-info {
        display: flex;
        flex-direction: column;
        margin-bottom: 30px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
        }

        a{
            &:hover{
                color: $blue-light;
            }
        }
    }

    &__contacts-text {
        display: inline-flex;
        align-items: center;
        text-decoration: none;
        font-size: 16px;
        line-height: 20px;
        color: $gray;
        margin-bottom: 10px;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 140%;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__socials {
        display: block;
    }

    &__socials-title {
        color: $dark;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        text-transform: uppercase;
        margin-bottom: 15px;
    }

    &__socials-list {
        display: flex;
        align-items: center;
    }

    &__socials-item {
        width: 24px;
        height: 24px;
        margin-right: 10px;
        fill: #000;
        transition: fill 0.35s ease;

        &:hover{
            fill: $blue-light;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }
}