@import "@theme/vars.scss";


.article {

    &__inner {
    }

    &__img {
        display: block;
        width: 100%;
        height: 550px;
        margin-bottom: 30px;
        border-radius: 15px;
        overflow: hidden;

        @media only screen and (max-width: $bp-lg) {
            height: 500px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__content {
        padding: 0 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 0;
        }
    }

    &__block {
        font-family: 'CenturyGothic';
        color: $blue-dark;
        margin-bottom: 30px;
        max-width: 1152px;

        @media only screen and (max-width: $bp-xxl) {
            max-width: none;
        }

        &:last-child{
            margin-bottom: 0;
        }

        p{
            font-family: 'CenturyGothic';
            font-size: 16px;
            line-height: 155%;
            margin-bottom: 10px;

            @media only screen and (max-width: $bp-lg) {
                font-size: 14px;
                line-height: 145%;
            }

            span{
                font-weight: 700;
            }

            &:last-child{
                margin-bottom: 0;
            }
        }

        h3{
            font-size: 25px;
            line-height: 31px;
            margin-bottom: 20px;
        
            @media only screen and (max-width: $bp-lg) {
                font-size: 22px;
                line-height: 27px;
                margin-bottom: 15px;
            }

            @media only screen and (max-width: $bp-md) {
                font-size: 18px;
                line-height: 145%;
            }
                
            &:last-child{
                margin-bottom: 0;
            }
        }

        a{
            font-weight: 700;
            color: $blue-light;
            transition: color 0.35s ease;

            &:hover{
                color: $blue;
            }
        }
    }
}
