@import "@theme/vars.scss";


.status-label {
    box-sizing: border-box;
    display: flex;
    position: relative;

    &.is-loading &__wrapper--loading{
        display: flex;
        animation: fadeIn 0.75s ease forwards;
    }

    &.is-error &__wrapper--error{
        display: flex;
        animation: fadeIn 0.75s ease forwards;
    }

    &.is-ok &__wrapper--ok{
        display: flex;
        animation: fadeIn 0.75s ease forwards;
    }

    &__wrapper {
        display: none;
        align-items: center;
        height: 45px;
        padding: 0 10px;
        border-radius: 7px;
        background-color: #fff;
    }

    &__wrapper--loading &__icon{
        animation: rotate 2s linear infinite;
    }

    &__icon {
        margin-right: 10px;
    }

    &__title {
        color: $blue-dark;
        white-space: nowrap;
    }
}
