@import '@theme/vars.scss';
$tooltip-color:                     $blue-dark;
$tooltip-bg:                        #fff;
$tooltip-border-radius:             10;
$tooltip-opacity:                   1;

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";
@import "~bootstrap/scss/tooltip";
@import "~bootstrap/scss/transitions";

.tooltip{
    &-inner{
        box-shadow: 4px 5px 10px rgba(40, 107, 164, 0.2);
        // background-color: #fff;
        padding: 10px;
        border-radius: 6px;
        // color: $blue-dark;
        // opacity: 1;
        font-size: 14px;
        line-height: 135%;
        font-family: 'CenturyGothic';
    }

    &-trigger{
        display: flex;
        width: 16px;
        height: 15px;
        box-shadow: -1px 3px 18px rgba(255, 204, 0, 0.5);
        transition: box-shadow 0.35s ease;

        &:hover{
            box-shadow: -1px 3px 18px rgba(255, 204, 0, 0);
        }

        &__icon{
            width: 100%;
            height: 100%;
        }
    }
}