@import "@theme/vars.scss";


.section-special {

    &__inner {
        overflow: hidden;
        display: flex;
        justify-content: space-between;
        padding: 60px;
        background: linear-gradient(336.84deg, rgba(0, 48, 66, 0.2) -13.89%, rgba(0, 48, 66, 0) 111.56%), #FFFFFF;
        border-radius: 15px;
        color: $blue-dark;

        @media only screen and (max-width: $bp-xl) {
            padding: 40px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 38px;
            flex-direction: column;
            align-items: center;
        }
    }

    &__main {
        width: 478px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xl) {
            width: 435px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            max-width: 350px;
        }
    }

    &__stats {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 10px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 5px;
            justify-content: center;
        }
    }

    &__stat {
        display: flex;
        flex-direction: column;
        margin-right: 50px;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 35px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-right: 20px;
            margin-bottom: 10px;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }

    &__stat-title {
        margin-bottom: 10px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 26px;
            line-height: 125%;
            margin-bottom: 5px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 22px;
            line-height: 27px;
        }
    }

    &__stat-subtitle {
        @media only screen and (max-width: $bp-xl) {
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 12px;
            line-height: 15px;
        }
    }

    &__title {
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 30px;
            line-height: 135%;
            margin-bottom: 15px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 10px;
            padding-right: 20px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 24px;
            line-height: 125%;
            padding-right: 0;
            text-align: center;
        }
    }

    &__subtitle {
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 16px;
            margin-bottom: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            margin-bottom: 20px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 12px;
            line-height: 17px;
            margin-bottom: 15px;
            text-align: center;
        }

        span{
            font-weight: 700;
        }
    }

    &__form {
        width: 100%;
        
        @media only screen and (max-width: $bp-md) {
            margin: 0 auto;
        }

        form{
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            @media only screen and (max-width: $bp-md) {
                align-items: initial;
            }
        }

        @media only screen and (max-width: $bp-md) {
            max-width: 260px;
        }
    }

    &__input {
        margin-bottom: 15px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 10px;
        }

        &:last-of-type{
            margin-bottom: 36px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: 20px;
            }
        }
    }

    &__button {
    }



    &__second {
        position: relative;
        margin-left: 50px;
        flex-grow: 1;

        @media only screen and (max-width: $bp-xl) {
            margin-left: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-left: 0;
        }

        @media only screen and (max-width: $bp-md) {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            max-width: 350px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        position: relative;
        z-index: 1;
        max-width: 445px;

        @media only screen and (max-width: $bp-xl) {
            max-width: none;
        }

        @media only screen and (max-width: $bp-md) {
            align-items: center;
            padding: 0 20px;
        }
    }

    &__info-text {
        margin-bottom: 25px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 12px;
            line-height: 145%;
            margin-bottom: 20px;
        }

        @media only screen and (max-width: $bp-md) {
            text-align: center;
            margin-bottom: 15px;
        }

        span{
            font-weight: 700;
        }
    }

    &__devices {
        display: flex;
        flex-wrap: wrap;
    }

    &__device {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: 24px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-right: 16px;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }

    &__device-img {
        display: block;
        display: flex;
        width: 70px;
        height: 70px;
        object-fit: contain;
        flex-shrink: 0;
        margin-bottom: 10px;

        @media only screen and (max-width: $bp-xl) {
            width: 56px;
            height: 56px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 44px;
            height: 44px;
            margin-bottom: 5px;
        }
    }

    &__device-title {
        font-family: 'CenturyGothic';
        font-weight: 400;

        @media only screen and (max-width: $bp-lg) {
            font-size: 12px;
            line-height: 135%;
        }
    }

    &__img {
        display: flex;
        position: absolute;
        bottom: -60px;
        right: -180px;
        width: 130%;
        min-width: 750px;

        @media only screen and (max-width: $bp-xl) {
            bottom: -40px;
            right: -80px;
            min-width: 500px;
            max-width: 625px;
            width: 110%;
        }

        @media only screen and (max-width: $bp-lg) {
            min-width: 410px;
            max-width: 520px;
        }

        @media only screen and (max-width: $bp-md) {
            position: relative;
            height: 246px;
            order: -1;
            bottom: 0;
            right: 0;
            width: 100%;
            margin-left: auto;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;

            @media only screen and (max-width: $bp-md) {
                width: auto;
            }
        }
    }
}
