@import "@theme/vars.scss";

.card-article {
    overflow: hidden;
    position: relative;
    box-sizing: border-box;
    display: block;
    height: 350px;
    width: 100%;
    padding: 40px;
    border-radius: 15px;
    background-color: #fff;
    text-decoration: none;
    color: $gray;
    border: 1px solid #AFBECB;
    font-family: 'Helvetica';
    transition: border-color 0.35s ease;

    @media only screen and (max-width: $bp-lg) {
        padding: 30px;
        height: 315px;
    }

    @media only screen and (max-width: $bp-md) {
        height: auto;
        min-height: 184px;
        padding: 24px 30px;
    }

    &:hover{
        border-color: $blue-light;
    }

    &:hover &__title{
        color: $blue-light;
    }

    &:active{
        border-color: $blue-dark;
    }
    
    &__inner {
        position: relative;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        z-index: 2;
        height: 100%;
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: none;
    }

    &__info {
        display: flex;
        flex-direction: column;
        margin-bottom: auto;
    }

    &__date {
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
            line-height: 19px;
            margin-bottom: 15px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 12px;
            line-height: 14px;
            margin-bottom: 10px;
        }
    }

    &__title {
        -webkit-line-clamp: 4;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        line-height: 125%;
        font-family: 'CenturyGothic';
        color: #003042;
        margin-bottom: 20px;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
        }
        
        @media only screen and (max-width: $bp-md) {
            -webkit-line-clamp: 3;
            line-height: 135%;
            margin-bottom: 5px;
        }
    }

    &__text {
        -webkit-line-clamp: 3;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;

        @media only screen and (max-width: $bp-md) {
            -webkit-line-clamp: 2;
        }
    }

    &__link {
        margin-top: 25px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 15px;
            font-size: 16px;
            line-height: 19px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
            line-height: 17px;
        }
    }

    &--slide{
        color: #fff;
        height: 550px;
        border: 0;
        
        @media only screen and (max-width: $bp-lg) {
            height: 500px;
            padding: 40px 22px 75px;
        }

        &::after{
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            width: 100%;
            height: 100%;
            background: linear-gradient(179.91deg, rgba(2, 64, 87, 0) 0.08%, rgba(2, 63, 86, 0.0359149) 45.84%, #01222F 99.92%),
        }
    }

    &--slide:hover &__title{
        color: #fff;
    }

    &--slide &__inner{  
        flex-direction: row;
        align-items: flex-end;
        justify-content: space-between;
    }

    &--slide &__img{
        display: block;
    }

    &--slide &__info{
        margin-bottom: 0;
        max-width: 550px;

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
        }
    }

    &--slide &__date{
        order: 2;
        margin-bottom: 0;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 4px;
        }
    }

    &--slide &__title{
        font-size: 30px;
        line-height: 42px;
        color: #fff;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 22px;
            line-height: 135%;
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 135%;
        }
    }

    &--slide &__text{
        display: none;
    }

    &--slide &__link{
        color: #fff;

        @media only screen and (max-width: $bp-lg) {
            position: absolute;
            bottom: 64px;
            right: 22px;
            bottom: 0;
            right: 0;
            font-size: 18px;
            line-height: 145%;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            line-height: 145%;
        }

        .icon{
            margin-left: 5px;
        }
    }
}
