@import "@theme/vars.scss";

html {
  font-family: $defaultFontFamily;
  font-size: $defaultFontSize;
  line-height: $defaultLineHeight;
  color: $blue-dark;
  background-color: #FFF;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow-x: hidden;

    &._lock {
        overflow: hidden;
    }
}

*{
    -webkit-tap-highlight-color: #DEE5E8;
    scrollbar-color: $gold #DEE5E8;
    scrollbar-width: thin;
    
    &::-webkit-scrollbar {
        height: 8px;
        width: 8px;
    }

    &::-webkit-scrollbar-track {
        background-color: #DEE5E8;
        border-radius: 5px;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $gold;
        border-radius: 5px;
    }
}

img{
  image-rendering: -webkit-optimize-contrast;
}

.main{
    flex-grow: 1;
    overflow-x: hidden;
    width: 100%;
    padding-top: 62px;

    section{
        &:first-of-type{
            padding-top: 0;
        }

        &:last-child{
            padding-bottom: 50px;
        }
    }
}

.section{
    box-sizing: border-box;
    padding: 30px 0;

    &__header{
        padding: 0 32px;
        margin-bottom: 30px;
        
        @media only screen and (max-width: $bp-lg) {
            padding: 0;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
        }
    }

    &__title{
        color: #003042;
    }

    &__subtitle {
        margin-top: 10px;
        font-size: 16px;
        line-height: 22px;

        /* unactive */

        color: #7D8C99;
    }
    
    &__badges{
        box-sizing: border-box;
        margin-top: 20px;

        margin-right: -72px;
        padding-left: 72px;
        margin-left: -72px;
        padding-right: 72px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: -40px;
            padding-left: 40px;
            margin-left: -40px;
            padding-right: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 15px;
            margin-right: -25px;
            padding-left: 25px;
            margin-left: -25px;
            padding-right: 25px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: -20px;
            padding-left: 20px;
            margin-left: -20px;
            padding-right: 20px;
        }


        -webkit-tap-highlight-color: transparent;
        scrollbar-color: transparent transparent;
        scrollbar-width: thin;
        
        &::-webkit-scrollbar-track {
            background-color: transparent;
        }

        &::-webkit-scrollbar-thumb {
            background-color: transparent;
        }
    }

    &__inner{
        position: relative;
        z-index: 1;
    }
}


.container {
    box-sizing: border-box;
    width: 100%;
    max-width: 1440px;
    padding: 0 40px;
    margin: 0 auto;

    @media only screen and (max-width: $bp-lg) {
        padding: 0 25px;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 0 20px;
    }
}