@import "@theme/vars.scss";

.card-payment {
    box-sizing: border-box;
    position: relative;
    display: flex;
    background: linear-gradient(336.84deg, rgba(0, 48, 66, 0.2) -13.89%, rgba(0, 48, 66, 0) 111.56%), #FFFFFF;
    border-radius: 15px;
    padding: 30px;
    height: 350px;
    text-decoration: none;
    color: $blue;

    @media only screen and (max-width: $bp-xl) {
        height: 320px;
    }

    @media only screen and (max-width: $bp-lg) {
        height: 275px;
    }

    @media only screen and (max-width: $bp-md) {
        min-height: 175px;
        height: auto;
    }

    &:hover &__title{
        color: $blue-light;
    }

    &:hover &__img{
        transform: scale(105%);
    }

    &__inner {
        position: relative;
        z-index: 1;
    }

    &__title {
        margin-bottom: 20px;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 10px;
        }
    }

    &__text {
        margin-bottom: 10px;
        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
        }
    }

    &__link {
        color: $blue-dark;

        span + .icon{
            margin-left: 5px;
        }
    }

    &__img {
        display: flex;
        max-width: 100%;
        height: 350px;
        position: absolute;
        bottom: 0;
        right: 0;
        transform-origin: right bottom;
        will-change: transform;
        transition: transform 0.65s ease;

        @media only screen and (max-width: $bp-xl) {
            height: 300px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 220px;
            max-width: 50%;
        }

        img{
            width: 100%;
            height: auto;
            object-fit: cover;
            object-position: right bottom;

            @media only screen and (max-width: $bp-md) {
                object-fit: contain;
            }
        }
    }
}