@import "@theme/vars.scss";

.section-payment {

 
    &__list {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -7.5px -15px;
        min-width: 100%;


        @media only screen and (max-width: $bp-md) {
            margin: 0 0 -10px;
        }

        .card-payment{
            width: calc(25% - 15px);
            margin: 0 7.5px 20px;

            @media only screen and (max-width: $bp-xxl) {
                width: calc(33.33% - 20px);
            }

            @media only screen and (max-width: $bp-lg) {
                width: calc(50% - 20px);
                margin-bottom: 15px;
            }

            @media only screen and (max-width: $bp-md) {
                width: 100%;
                margin: 0 0 10px;
            }
        }
    }
}
