@import "@theme/vars.scss";

.form-block{
    box-sizing: border-box;
    position: relative;
    display: block;
    background: linear-gradient(14.5deg, rgba(0, 48, 66, 0.11) -21.24%, rgba(0, 48, 66, 0) 176.31%), #FFFFFF;
    border-radius: 10px;
    padding: 75px 75px 75px 35px;
    min-height: 252px;
    overflow: hidden;

    @media only screen and (max-width: $bp-lg) {
        padding: 35px 105px 35px 35px;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 35px;
    }

    &__main {
        position: relative;
        z-index: 1;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__title {
        margin-right: 80px;
        color: $blue-dark;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 60px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 24px;
            line-height: 120%;
            margin-right: 0;
            margin-bottom: 30px;
            text-align: center;
        }
    }

    &__form {
        display: flex;
        flex-grow: 1;

        @media only screen and (max-width: $bp-xl) {
            margin: 0 auto;
            max-width: 300px;
            width: 100%;
        }

        @media only screen and (max-width: $bp-lg) {
            margin: 0;
            max-width: 280px;
        }

        form{
            width: 100%;
            display: flex;

            @media only screen and (max-width: $bp-xl) {
                flex-direction: column;
            }
        }
    }

    &__input {
        margin-right: 20px;
        max-width: 280px;
        
        @media only screen and (max-width: $bp-xl) {
            margin-right: 0;
            margin-bottom: 15px;
            max-width: 300px;

            &:last-of-type{
                margin-bottom: 20px;
            }
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: 280px;
        }
    }

    &__button-wrapper{
        position: relative;
        flex-shrink: 0;
    }

    &__button{
        width: 100%;
    }

    &__status{
        position: absolute;
        top: 100%;
        left: 0;
        transform: translateY(20px);
        
        @media only screen and (max-width: $bp-xl) {
            margin-top: 0;
            left: auto;
            right: 100%;
            top: 50%;
            transform: translate(-20px, -50%);
        }

        @media only screen and (max-width: $bp-md) {
            position: static;
            transform: none;
            margin: 15px auto 0;
            justify-content: center;
        }
    }

    &__bg {
        display: block;
        object-fit: contain;
        position: absolute;
        height: 100%;
        width: auto;

        
        &--1 {
            left: 0;
            top: 0;
            object-position: right center;
            object-fit: cover;

            @media only screen and (max-width: $bp-xl) {
                max-width: 260px;
            }

            @media only screen and (max-width: $bp-md) {
                max-width: 300px;
            }
        }

        &--2 {
            top: 0;
            height: 300px;
            right: -180px;
            object-fit: contain;

            @media only screen and (max-width: $bp-xl) {
                height: 215px;
                right: -126px;
                top: 10px;
            }

            @media only screen and (max-width: $bp-md) {
                height: 124px;
                right: -45px;
                top: auto;
                bottom: -25px;
            }
        }
    }
}
