@import "@theme/vars.scss";

.card-television {
    box-sizing: border-box;
    width: 100%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    padding: 35px;
    border: 1px solid $blue-white;
    border-radius: 15px;
    text-decoration: none;
    transition: border-color 0.45s ease;
    background: linear-gradient(336.84deg, rgba(0, 48, 66, 0.08) -13.89%, rgba(0, 48, 66, 0) 111.56%), #FFFFFF;

    @media only screen and (max-width: $bp-lg) {
        padding: 20px;
        min-height: 280px;
    }

    @media only screen and (max-width: $bp-md) {
        min-height: 220px;
    }

    &__title-wrapper{
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background-color: #FECC00;
        border-radius: 10px;
        padding: 10px 20px;
        margin-right: auto;
        text-decoration: none;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            padding: 10px 15px;
            margin-bottom: 15px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 10px;
            margin-bottom: 10px;
        }
    }

    &__title{
        background: linear-gradient(148.05deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.4) 0.01%, rgba(255, 255, 255, 0) 100%), #010E12;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        text-align: center;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
            line-height: 27px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 16px;
            line-height: 20px;
        }
    }

    &__list{
        p{

            @media only screen and (max-width: $bp-lg) {
                font-size: 16px;
                line-height: 19px;
            }

            @media only screen and (max-width: $bp-md) {
                font-size: 14px;
                line-height: 17px;
            }

            span{
                font-weight: 700;
            }
        }
    }

    &__list-item{
        color: $blue-dark;
        margin-bottom: 5px;
    }

    &__link{
        font-family: 'HelveticaNeue';
        display: flex;
        margin-bottom: 20px;
        font-weight: 500;
        text-decoration: underline;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
            font-size: 16px;
            line-height: 19px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 10px;
            font-size: 14px;
            line-height: 17px;
        }
    }

    &__price {
        display: flex;
        font-family: 'Helvetica';
        font-weight: 700;
        font-size: 30px;
        line-height: 135%;
        color: $blue-dark;
        margin-bottom: 5px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 24px;
        }
        
        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
        }
    }

    &__button {
        color: $blue-dark;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 5px;
            margin-left: 5px;
            font-size: 14px;
        }

        @media only screen and (max-width: $bp-md) {
            margin: 5px 3px 0;
        }
        
        .icon{
            @media only screen and (max-width: $bp-lg) {
                width: 20px;
                height: 20px;
            }

            @media only screen and (max-width: $bp-md) {
                width: 16px;
                height: 16px;
            }
        }

        span + .icon{
            margin-left: 5px;
        }

        &:hover{
            color: $blue-light;
        }
    }

}
