@import "@theme/vars.scss";


.equipment {

    .section {

        &__inner {
            padding: 0 32px;

            @media only screen and (max-width: $bp-xl) {
                padding: 0;
            }
        }
    }

    &__content {
        display: flex;
        flex-wrap: wrap;
    }

    &__first {
        width: 624px;
        margin-right: 85px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xxl) {
            width: 560px;
            margin-right: 50px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 480px;
            margin-right: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-right: 0;
            margin-bottom: 30px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 20px;    
        }
    }

    &__header {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
        width: 100%;
    }

    &__subtitle {
        color: #7D8C99;
    }

    &__title {
        font-size: 30px;
        line-height: 135%;
        color: $blue;

        @media only screen and (max-width: $bp-xxl) {
            font-size: 28px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 24px;
        }
    }

    &__slider-img {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: baseline;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__slider {
        display: flex;
        height: 500px;

        @media only screen and (max-width: $bp-xxl) {
            height: 440px;
        }

        @media only screen and (max-width: $bp-xl) {
            flex-direction: column;
            height: auto;
        }

        @media only screen and (max-width: $bp-lg) {
            flex-direction: row;
            height: 440px;
        }

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            height: auto;
        }
    }

    &__slider-main {
        margin-right: 15px;
        width: 544px;
        height: 100%;

        @media only screen and (max-width: $bp-xxl) {
            width: 480px;
        }

        @media only screen and (max-width: $bp-xl) {
            margin-right: 0;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 540px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            max-width: 600px;
            height: auto;
            aspect-ratio: 1;
        }
                
        .slider {

            &__inner {
                height: 100%;
            }

            &__wrapper {
            }

            &__slide {
                box-sizing: border-box;
                display: flex;
                align-items: center;
                width: 100%;
                height: 100%;
                border-radius: 15px;
                border: 1px solid #7D8C99;
                overflow: hidden;
                background-color: #fff;

                &.swiper-slide-active{

                }
            }


            &__navigation {
            }

            &__button {
            }

            &__button--prev {
            }

            &__button-icon {
            }

            &__button--next {
            }

            &__pagination {
            }
        }

    }

    &__slider-thumbs {
        height: 100%;
        flex-shrink: 0;
        width: max-content;

        @media only screen and (max-width: $bp-xl) {
            margin-top: 20px;
            width: auto;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-left: 20px;
            margin-top: 0;
            width: max-content;
        }

        @media only screen and (max-width: $bp-md) {
            margin-left: 0;
            margin-top: 15px;
            width: auto;
        }
                
        .slider {

            &__inner {
                max-height: 100%;
            }

            &__slide {
                box-sizing: border-box;
                cursor: pointer;
                display: flex;
                align-items: center;
                width: 65px;
                aspect-ratio: 1;
                border-radius: 5px;
                border: 1px solid #7D8C99;
                overflow: hidden;
                transition: border-color 0.4s ease;

                &.swiper-slide-thumb-active{
                    border-color: $gold;
                }
            }
        }
    }

    &__main {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex-grow: 1;
        max-width: calc(100% - 709px);

        @media only screen and (max-width: $bp-xxl) {
            max-width: calc(100% - 610px);
        }
        
        @media only screen and (max-width: $bp-xl) {
            max-width: calc(100% - 520px);
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
            width: 100%;
        }
    }

    &__main &__parameter-col{
        &:first-of-type{
            max-width: 260px;
        }
    }

    &__parameters {
        width: 100%;
    }

    &__parameters-group {
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__parameters-title {
        color: #7D8C99;
        margin-bottom: 10px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
        }
    }

    &__parameters-list {
        color: $blue-dark;

        .list {

            &__item {
                
            }
        }
    }

    &__parameter-row{
        display: flex;
        width: 100%;
    }

    &__parameter-col{
        display: flex;
        box-sizing: border-box;
        width: 50%;

        &:first-of-type{
            max-width: 510px;
            padding-right: 10px;
            
            p{
                font-weight: 700;
            }
        }

        &:last-of-type{
            flex-grow: 1;
            flex-shrink: 0;
        }

        p{
            font-family: 'Century Gothic';
            font-size: 16px;
            line-height: 125%;

            @media only screen and (max-width: $bp-xl) {
                font-size: 14px;
                line-height: 135%;
            }
        }

        .icon{
            width: 12px;
            height: 20px;
        }
    }

    &__tabs &__parameter-col{
        &:first-of-type{
            width: 100%;
        }
        &:last-of-type{
            width: auto;
        }
    }


    &__special {
        position: relative;
        box-sizing: border-box;
        display: block;
        max-width: 100%;
        width: auto;
        padding: 20px;
        background: linear-gradient(339.14deg, rgba(0, 48, 66, 0.2) -93.84%, rgba(0, 48, 66, 0) 142.91%), #FFFFFF;
        box-shadow: 9px 12px 33px 5px rgba(18, 66, 111, 0.05);
        border-radius: 5px;
        margin-top: 30px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 20px;
            padding: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-top: 15px;
            padding: 14px;
            border-radius: 4px;
        }
    }

    &__special-title {
        font-weight: 500;
        width: 100%;
        color: #1C1C1C;
        margin-bottom: 10px;
        
        @media only screen and (max-width: $bp-md) {
            margin-bottom: 6px;
            font-size: 10px;
            line-height: 12px;

        }

        span{
            font-weight: 700;
        }
    }

    &__special-info{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__special-price {
        margin-right: 25px;
    }

    &__special-button {
        padding: 0 30px;

        @media only screen and (max-width: $bp-lg) {
            padding: 0 20px;
            font-size: 14px;
            height: 38px;

            .icon{
                width: 20px;
                height: 20px;
            }
        }
        @media only screen and (max-width: $bp-md) {
            height: 32px;
        }
    }

    &__special-img {
        position: absolute;
        right: -42px;
        bottom: -32px;
        width: 90px;
        height: 80px;
        z-index: 1;

        @media only screen and (max-width: $bp-lg) {
            width: 75px;
            height: 68px;
            right: -36px;
            bottom: -28px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 60px;
            height: 54px;
            right: -30px;
            bottom: -25px;
        }
    }



    &__tabs {
        width: 100%;
        margin-top: 50px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 30px;
        }
    }

    &__tabs-nav {
        margin-bottom: 30px;
        

    }

    &__tabs-content {
    }

    &__tabs-item {
        display: none;

        &.is-active{
            display: block;
            animation: fadeInTop 0.55s ease forwards;
        }
    }

    &__parameters-group {
    }
}

